.stock {
	width: 80px;
	height: 80px;
	background-color: #c35a3f;
	border: 1px solid white;
	color: $white;
	font: 300 12px "Din Light", sans-serif;
	text-transform: uppercase;
	text-align: center;
	padding-top: 45px;
	position: fixed;
	top: 38px;
	left: 38px;
	border-radius: 50%;
	@include r(1200) {
		top: 20px; }
	@include r(767) {
		display: none; }
	&:before {
		content: '';
		@include vcenter;
		@include s(box);
		top: 38%; } }
.fixed-line {
	background-color: #2e487e;
	background-image: linear-gradient(165deg, rgba(143, 105, 119, 0.04) 0%, transparent 100%);
	padding: 0 25px; }
.fixed-line__logo {
	display: inline-block;
	width: 110px;
	height: 46px;
	@include image("img/d-topline-logo.png");
	@include cover; }
.is-night-js {
	.fixed-line__logo {
		@include image("img/n-topline-logo.png"); } }
