.container, .container-large {
	margin: 0 auto;
	position: relative;
	padding: 0 15px;
	@include rmin(320) {
		width: 100%; }
	@include rmin(768) {
		width: 750px; }
	@include rmin(992) {
		width: 970px; }
	@include rmin(1200) {
		width: 1030px; } }
.container-large {
	@include rmin(1200) {
		width: 80%; }
	@include rmin(1500) {
		width: 1460px; } }
